.root {
    display: flex;
    flex-direction: column;
    align-items: center;
    .container {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 20px 0;
      margin: 30px;
      background: linear-gradient(
        135deg,
        rgba(64, 64, 63, 0.1),
        rgba(6, 85, 164, 0),
        rgba(6, 85, 164, 0)
      );
      backdrop-filter: blur(10px);
      border-radius: 20px;
      border: 1px solid rgba(8, 8, 8, 0.18);
      box-shadow: 0 8px 30px 0 rgba(112, 110, 110, 0.977);
      img {
        max-width: 400px;
        margin-right: 1rem;
        margin-left: 1rem;
      }
      .sum {
        h5 {
          font-size: 28px;
          text-align: center;
        }
  
        > div {
          margin: 0 20px;
        }
  
        .daysPicker {
          display: flex;
          flex-direction: row;
          justify-content: center;
          align-items: center;
          p {
            padding: 10px;
            margin: 10px;
            font-size: 24px;
          }
        }
  
        .price {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          h5 {
            font-size: 28px;
          }
        }
      }
    }
  }
  @media (max-width: 900px) {
    .root {
      .container {
        flex-direction: column;
      }
    }
  }
  
  @media (max-width: 500px) {
    .root {
      .container {
        .sum {
          padding: 0 10px;
          flex-direction: column;
          > div {
            margin: 10px;
          }
          h5 {
            font-size: 20px;
          }
          .price {
            h5 {
              font-size: 22px;
            }
            p {
              font-size: 18px;
            }
          }
        }
      }
    }
  }